@keyframes borderColors { 
  0%   {border-color: green; border: 5px solid !important;}
  25%  {border-color: yellow; border: 5px solid !important;}
  50%  {border-color: blue; border: 5px solid !important;}
  65%  {border-color: red; border:  5px solid !important;}
  100% {border-color: white; border: 5px solid !important;}
 }

 section .card {
  animation-duration: .7s;
  animation-name: borderColors;
  animation-timing-function: ease-in-out;
  /* animation-iteration-count: infinite; */
 }

 .footer{
  animation-duration: 1s;
  animation-name: borderColors;
  animation-timing-function: ease-in-out;
  /* animation-iteration-count: infinite; */
 }

input:focus {
  outline: 2px;
  outline-color: white;
  background-color: aliceblue;
}

body,
html {
  margin: 0;
  padding: 0;
  position: sticky;
}


body {
  display: flex;
  flex: 0 0 auto;
  flex-basis: 100%;
  height: 100%vh;
  flex-direction: row;
  font-family: 'Oxygen', sans-serif;
  font-weight: 200;
  background-color: #F5F5F5;
}

.visible {
  display: flex !important;
  visibility: visible;
}

#container {
  display: flex;
  flex-basis: 100%;
  flex: auto;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0;
  padding: 0;
  /* overflow: visible; */
  overflow-wrap: wrap;
  background-color: #fff;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  top: 0;
  height: 100vh;
  right: 0;
  left: 0;
  position: fixed;
  order: 1;
  margin-left: auto;
  margin-right: auto;
}
/* Background */

#background {
  display: flex;
  flex-basis: 100%;
  background: #fff;
  bottom: 0;
  direction: ltr;
  left: 0;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  z-index: -100;
 }

::-webkit-scrollbar {
  height: 10px;
  overflow: visible;
  background: white;
  width: 12px;
}
::-webkit-scrollbar-button {
  height: 0;
  width: 0;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background-color: rgb(27, 127, 170);
  opacity: .65;
  background-clip: padding-box;
  border: solid transparent;
  border-width: 1px 1px 1px 6px;
  min-height: 28px;
  padding: 100px 0 0;
  -webkit-box-shadow: inset 1px 1px 0 rgba(0,0,0,.1), inset 0 -1px 0 rgba(0,0,0,.07);
  box-shadow: inset 1px 1px 0 rgba(0,0,0,.1), inset 0 -1px 0 rgba(0,0,0,.07);
}
::-webkit-scrollbar-track {
  background-clip: padding-box;
  border: solid transparent;
  border-width: 0 0 0 4px;
}

.border {
  border: 1px solid red;
}

/* Header */
header {
  display: flex;
  flex: auto;
  flex-basis: 10%;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  overflow: hidden;
  background-color: rgb(255, 255, 255);
  order: 1;
}

header * {
  font-size: 20px;
}

header #left_menu {
  display: flex;
  color:   rgb(27, 127, 170);
  background-color: white;
  flex: auto;
  flex-direction: row;
  flex-basis: 20%;
  align-items: center;
  align-content: center;
  justify-content: center;
  text-decoration: none;
  margin-left: auto;
  margin-right: auto;
  order: 1;
}

#main_menu {
  display: flex;
  flex: auto;
  flex-direction: row;
  flex-basis: 100%;
  position: relative;
  margin-left: 0;
  margin-right: auto;
}

#main_menu ul {
  display: flex;
  text-decoration: none;
  margin-left: 0;
  margin-right: auto;
}

header #right_menu {
  display: flex;
  flex: auto;
  flex-basis: 50%;
  flex-direction: row;
  overflow-wrap: normal;
  align-content: center;
  align-items: center;
  justify-content: space-around;
  align-self: center;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  order: 2;
  clear: right;
}

#right_menu #title {
  display: flex;
  flex: flex-shrink;
  position: relative;
  overflow-wrap: wrap;
  background: url(data:image/png;base64,);
  background-repeat: repeat;
  background-position: center;
  background-size: cover;
  background-color: white;
  height: 33.5px;
  width: 100px;
  clear: right;
  margin: 1em;
  padding: 0;
  z-index: 2;
  order: 2;
  margin-left: 0;
  margin-right: auto;
}

#right_menu #img {
  display: flex;
  flex: flex-shrink;
  position: relative;
  overflow-wrap: wrap;
  background: url(data:image/png;base64,);
  background-repeat: repeat;
  background-position: center;
  background-size: cover;
  background-color: white;
  height: 45px;
  width: 300px;
  margin: 2em;
  z-index: 1;
  order: 1;
  margin-left: 0;
  margin-right: auto;
}

/* The container <div> - needed to position 
   the dropdown menu */


#menu_options {
  display: none;
  margin-left: 0;
  /* align-self: flex-start; */
  flex-basis: 100%;
  background-color:  rgb(27, 127, 170);
  opacity: .75;
  /* background-color: rgb(27, 127, 170); */
  border-bottom: .5em double white;
  border-right: .5em double white;
}

#menu_options ul {
  display: flex;
  flex: auto;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  flex: auto;
}
#menu_options * {
  display: flex;
  text-decoration: none;
  text-align: center;
  align-items: center;
  justify-content: center;
  outline: none;
  color: white;
  min-width: 100%;

}

#menu_options ul li a {
  padding: 1em;
  flex: auto;
  min-width: 100%;
  flex-basis: 100%;
}

#menu_options ul li {
  flex: auto;
  min-width: 100%;
  flex-basis: 100%;
}

/* #menu_options ul li :last-child :not(.menuTitle) {
  border-right: double green;
} */

/* #menu_options ul :not(.menuTitle) {
  border-left: double green;
} */
.menu .menuTitle, .menu .menuItem {
  display: flex;
    flex: auto;
    color: #0d64bc;
    justify-content: center;
    text-align: center;
    padding: 1.5em;
}


/* dropdown menu items */

.menuItem:hover {
  /*background-color: #0d64bc;*/
  color: white;
  cursor: pointer;
  background-color: green;
  box-shadow:2PX 2px 2px 0  rgba(60, 64, 67, 0.30), 0 1px 6px 2px  rgba(60, 64, 67, 0.15);
}

.menuItem:active {
  background-color: purple;
  box-shadow: 0 3px 4px 0 rgba(60, 64, 67, 0.30), 0 5px 12px 2px rgba(60, 64, 67, 0.15);
}

/*  Main View >> Menu and Panes container */
#main_view {
  display: flex;
  flex-basis: 100%;
  height: 100vh;
  margin-top: 0;
  margin-bottom: 0;
  flex-direction: column;
  overflow: overlay;
  align-self: auto;
  justify-content: space-between;
  order: 2;
  margin-left: 0;
  margin-right: 0;
}

#firstHalf {
  display: none;
  visibility: hidden;
  flex-basis: 15%;
  height: 100%vh;
  flex: auto;
  justify-content: center;
  position: fixed;
  order: 1;
  margin-left: auto;
  margin-right: auto;
  z-index: 10;
  /* overflow: hidden; */
  transition: all 2s;
}

#secondHalf {
  display: flex;
  /* flex: auto;
  flex-wrap: wrap; */
  flex-direction: column;
  position: relative;
  justify-content: flex-start;
  /* align-items: center; */
  flex-basis: 100%;
  order: 2;
  margin: 0;
  margin-left: 0;
  margin-right: 0;
  /* overflow: hidden; */
  z-index: 9;
}

/*  Button container for component close/exit buttons 

*/
.buttonContainer {
  display: flex;
  justify-content: space-between;
  position: relative;
  flex-direction: row;
  flex-basis: 100%;
  height: auto;
  overflow: visible;
}


.exitbtn {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: flex-end;
  flex-direction: row;
  border-radius: 4px;
  background-color: #f44260;
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 14px;
  padding: 6px;
  width: 100px;
  cursor: pointer;
  margin: 5px;
  position: relative;
}

/*

views_pane card containers

*/

#views_pane {
  display: flex;
  flex: auto;
  position: relative;
  height: auto;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  order: 3;
  overflow-x: overlay;
  margin-left: 0;
  margin-right: 0;
}

#views_pane .card::-webkit-scrollbar {
  display: none !important;
} */

#results {
  display: flex;
  flex: auto;
  flex-basis: 2%;
  height: auto;
  flex-direction: row;
  justify-content: center;
  overflow: wrap;
  order: 4;
}

/*  Card Styling that styles the dynamically
    inserted containers of list items
*/
.card {
  display: flex;
  flex-basis: auto !important;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  text-align: center;
  opacity: 1.8;
  background-color: #fff;
  border-bottom: 2px solid rgba(0, 0, 0, 0.12);
  border-radius: 12px;
  box-shadow: 0 1.5px 2px 0 rgba(60, 64, 67, 0.30), 0 2px 6px 2px rgba(60, 64, 67, 0.15);
  /* overflow: scroll; */
  min-height: auto;
  min-width: auto;
  margin-left: auto;
  margin-right: auto;
}

/* .card #map {
  min-width: 25%;
  min-height: 100%;
} */

.card-header {
  display: flex;
  flex: auto;
  flex-basis: auto !important;
  flex-direction: row;
  border-style: hidden;
  align-self: center;
  justify-content: center;
  min-height: auto;
  min-width: auto;
  margin-left: auto;
  margin-right: auto;
}

