/* scriptWindow and Links inside the inserted cards */

/*Active class adds background color to 
  selected item*/

.active {
  background-color: rgb(27, 127, 170);
  opacity: .65;
  color: white !important;
}

.invalid {
  border-color: #F44260;
}

.valid {
  border-color: #47E88C;
}

#staticMapPrimaryContainer {
  display: flex;
  flex-basis: auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  text-align: center;
  visibility: visible;
  padding: 8px;
  margin-left: auto;
  margin-right: auto;
  z-index: 1;
  opacity: 1.7;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.30), 0 2px 6px 2px rgba(60, 64, 67, 0.15);
  transition: all 0s;
  order: 1;
  overflow: hidden;
}

#embedMap {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  align-self: center;
}

#scriptsPrimaryContainer {
    visibility: hidden;
    flex-basis: auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: relative;
    text-align: center;
    visibility: visible;
    padding: 2px;
    margin-left: auto;
    margin-right: auto;
    z-index: 1;
    opacity: 1.7;
    border: 1px solid rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.30), 0 2px 6px 2px rgba(60, 64, 67, 0.15);
    transition: all 0s;
    order: 1;
    overflow: hidden;
}

label {
  display: flex;
  text-align: center;
  font-size: 1.7em;
  flex-basis: 100%
}

p {
  margin: 5px;
  padding: .5em;
  display: flex;
  align-items: center;
  text-align: center;
  clear: right;
}

.input {
  display: flex;
  flex-basis: 100%;
  height: auto;
  overflow: visible;
}

.input::placeholder {
  font-size: 1.3em;
  overflow: visible;
  color:red;
}

#scriptsPrimaryContainer li {
  margin: 5px;
  cursor: pointer;
  font-size: 1.1em;
  text-align: center;
  padding: 12px 16px;
  text-decoration: none;
  display: flex;
  align-items: flex-start;
  overflow: hidden;
}

/* Change color of dropdown li items on hover */

#scriptsPrimaryContainer li:hover {
  background-color: #ddd;
  color:  rgb(27, 127, 170);
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.30), 0 2px 6px 2px rgba(60, 64, 67, 0.15);
}

#scriptsPrimaryContainer li:active {
  background-color: purple;
  color:rgb(27, 127, 170);
  box-shadow: 0 3px 4px 0 rgba(60, 64, 67, 0.30), 0 5px 12px 2px rgba(60, 64, 67, 0.15);
}

#scriptsPrimaryContainer li:focus-within li:hover {
  background-color: purple;
  color: rgb(27, 127, 170);
  opacity: .65;
  box-shadow: 0 3px 4px 0 rgba(60, 64, 67, 0.30), 0 5px 12px 2px rgba(60, 64, 67, 0.15);
}

/*Wrap the list with curves*/

#scriptsPrimaryContainer li:first-child {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

#scriptsPrimaryContainer li:last-child {
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}

#scriptsPrimaryContainer.show {
  visibility: visible;
  display: flex;
  position: relative;
  text-align: center;
  font-size: 1em;
}

#scriptList {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  align-self: center;
}

/* Start of Parameters Div*/

#secondaryContainer {
  visibility: hidden;
  display: flex;
  height: auto;
  justify-content: space-between;
  align-items: center;
  position: relative;
  list-style-type: none;
  flex-basis: 100%;
  z-index: 1;
  opacity: 1.7;
  margin-top: 5px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.30), 0 2px 6px 2px rg
}

#secondaryContainer.show {
  visibility: visible;
  display: flex;
  position: relative;
  text-align: center;
  font-size: .5em;
  flex-basis: 100%;
  min-width: 100%;
  /* align-items: flex-end; */
  justify-content: space-between;
}

.innerParam {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

#params, #parameters {
  min-width: auto;
  justify-content: center;
  align-self: center;
  text-align: center;
}

/* Footer */

footer {
  display: flex;
  flex-basis: .5%;
  flex: auto;
  height: auto;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  opacity: .65;
  bottom: 0;
  padding: 1em;
  text-align: right;
  color: white;
  overflow: hidden;
  order: 5;
  background-color: rgb(27, 127, 170);
  border: .2em solid white;
  border-top: .5em double !important;
  border-bottom: .2em solid !important;
}

footer p {
  display: flex;
  padding: 2em;
  align-content: center;
  justify-content: center;
  text-align: center;
  box-shadow: 2PX 2px 2px 0 white, 0 1px 6px 2px white;
}
